


export const getCookieFromReq = (req,cookie) => {
    const tokenCookie = req.headers.cookie.split(';').find(c => c.trim().startsWith(`${ccookie}=`));
            
    if(!tokenCookie) {return undefined}; // Return undefined if the expiresAtCookie is not available
    
    return cookie.split('=')[1];// split() => returns array of expiresAt text, where 2nd value'[1]' is date
}